body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes transition-bg {
  0% {
    background: #e9defa; }
  50% {
    background: #fbfcdb; }
  100% {
    background: #e9defa; } }

@keyframes transition-bg {
  0% {
    background: #e9defa; }
  50% {
    background: #fbfcdb; }
  100% {
    background: #e9defa; } }

.app {
  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  padding: 2em 0 10em 0; }
  .app .content-right {
    text-align: end; }
  .app h1 {
    text-align: center;
    text-shadow: 0 0 5px #6f6f70; }
  .app .boardArea {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start; }
  .app .board {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    box-shadow: inset 0 0 10px 5px #6abeef;
    border-radius: 4em;
    padding: 2em; }
    .app .board > .info {
      font-size: 1.2em;
      width: 14em;
      margin: 0 1em; }
      .app .board > .info > .line {
        height: 2.5em;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        border-bottom: 1px solid #bebebe; }
        .app .board > .info > .line > .title {
          width: 8em;
          font-weight: 500;
          cursor: default; }
        .app .board > .info > .line select {
          width: 6em;
          height: 2em;
          border-radius: 0.25em;
          font-size: 1.1em;
          -webkit-appearance: none;
          padding-left: 3em;
          background: transparent; }
        .app .board > .info > .line select:focus {
          outline: none; }
        .app .board > .info > .line button {
          width: 5em;
          border-radius: 0.25em;
          height: 2em;
          background: #bebebe;
          font-size: 0.9em; }
        .app .board > .info > .line button:focus {
          outline: none; }
        .app .board > .info > .line .restart {
          background: #5cb85c;
          border-color: #5cb85c; }
        .app .board > .info > .line .pause {
          background: #5bc0de;
          border-color: #5bc0de; }
        .app .board > .info > .line .animation {
          background: #FA8072;
          border-color: #FA8072; }
        .app .board > .info > .line span {
          text-shadow: 0 0 2px #000000; }
    .app .board > .zone > .matrix {
      display: flex;
      flex-direction: column; }
      .app .board > .zone > .matrix > .row {
        display: flex;
        flex-direction: row;
        border-top: 1px solid #9e9696; }
        .app .board > .zone > .matrix > .row > .cell {
          width: 2em;
          height: 2em;
          border-left: 1px solid #9e9696; }
          .app .board > .zone > .matrix > .row > .cell > .block {
            width: 2em;
            height: 2em;
            text-align: center;
            line-height: 2em;
            -webkit-transition: all ease 0.5s;
            transition: all ease 0.5s;
            background: #98c9f5; }
          .app .board > .zone > .matrix > .row > .cell > .revealed-block {
            width: 2em;
            height: 2em;
            text-align: center;
            line-height: 2em;
            -webkit-transition: all ease 0.5s;
            transition: all ease 0.5s;
            background: transparent; }
        .app .board > .zone > .matrix > .row > .cell:last-child {
          border-right: 1px solid #9e9696; }
        .app .board > .zone > .matrix > .row > .cell:hover {
          cursor: pointer; }
      .app .board > .zone > .matrix > .row:last-child {
        border-bottom: 1px solid #9e9696; }
    .app .board > section {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      margin-left: 1em;
      font-weight: 500; }
      .app .board > section > .title {
        font-size: 2em; }
      .app .board > section > .simple-description {
        display: flex;
        flex-direction: column;
        padding-left: 0.8em; }
      .app .board > section > .content {
        height: 400px;
        overflow-y: scroll;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start; }
        .app .board > section > .content > .line {
          margin: 0.6em 0; }
          .app .board > section > .content > .line .green {
            background: #5cb85c;
            padding: 0 0.2em;
            border-radius: 0.25em; }
          .app .board > section > .content > .line .pink {
            background: #FA8072;
            padding: 0 0.2em;
            border-radius: 0.25em; }
          .app .board > section > .content > .line .cyan {
            background: #5bc0de;
            padding: 0 0.2em;
            border-radius: 0.25em; }
      .app .board > section > .content::-webkit-scrollbar {
        -webkit-appearance: none;
        width: 6px;
        height: 5px;
        max-height: 10px; }
      .app .board > section > .content::-webkit-scrollbar-thumb {
        border-radius: 4px;
        background-color: #98c9f5; }
      .app .board > section > .content::-webkit-scrollbar-thumb:hover {
        opacity: 0.8;
        background-color: #98c9f580; }
      .app .board > section > .content::-webkit-scrollbar-thumb:window-inactive {
        background-color: #98c9f5; }

